<template>
  <div style="overflow:auto;height:90%;">
    <!-- 申请列表 -->
    <div v-if="applyList.length > 0"
         class="scroll">
      <div v-for="(item, i) in applyList"
           :key="i"
           style="background:#fff;margin-bottom:10px;"
           @click="goNext('/edit-or-see-apply',item)">
        <div class="info-hotel">
          <div class="hotel-left">
            <div style="width:80px;margin-right:8px;">
              <img :src="item.hotel.logo"
                   class='left-img'>
            </div>
            <div class="left-title">
              <div style="display:flex;align-items:center;">
                <div class="c_000">{{ item.hotel.hotelName }}{{ item.hotel.hotelName }}{{ item.hotel.hotelName }}</div>
                <div v-if="auditApprovalType"
                     style="font-size:0.6rem;border:1px solid #E5E2E5;padding:2px;margin-left:8px;white-space:nowrap;">{{ auditApprovalType[item.applyType || item.auditApproval.applyType] }}</div>
              </div>
              <div class="adr">{{ item.hotel.location || '暂无' }}</div>
              <div style="font-size:0.7rem;color:#888;">业务员:{{ item.hotel.businessmanager || '暂无' }}
              </div>
            </div>
          </div>
          <div class="hotel-right">
            <div v-if="item.applyType == '3' && item.status == '2' && item.business && item.business.payment == 0"
                 style="font-size: 0.6rem;color: #000;white-space: nowrap;">待支付:¥{{ item.business.realAmount | point }}</div>
            <div :style="{color:(item.status == '0' || item.status == '2'?'#ff7d41':'red')}">{{ statusAll[item.status] }}</div>
          </div>
        </div>
        <div style="display:flex;justify-content:flex-end;padding:10px 0;">
          <div v-if="item.applyType == '3' && item.status == '0' || item.applyType == '3' && item.status == '1'"
               @click.stop="cancelApply(item.id)"
               class="edit-reason">取消提交</div>
          <div v-if="item.applyType == '3' && item.status == '1'"
               @click.stop="rePush(item)"
               class="edit-reason">重新提交</div>
          <div v-if="item.applyType == '3' && item.status == '2' && item.business && item.business.payment == 0"
               class="edit-reason"
               @click.stop="rightPay(item)">立即支付</div>
        </div>
      </div>
    </div>
    <div v-else
         class="nothing">
      <img src="@/assets/equip/empty.png"
           alt="">
      <div>没有申请中订单</div>
    </div>
    <div @click="goNext('/add-apply','')"
         :class="applyList.length > 0 ? 'c_ff7d41':'c_fff'">新建申请</div>
    <!-- 立即付款 -->
    <van-popup v-model="showPay"
               position="bottom"
               style="width:100%;font-size: 0.8rem;color:#000;line-height:40px;">
      <div>
        <div style="border-bottom:6px solid #F7F4F8;font-weight:bold;">请选择支付方式</div>
        <div v-for="(item,i) in payList"
             :key='i'
             style="border-bottom:1px solid #F7F4F8;"
             @click="choosePay(item.type)">
          {{ item.title }}</div>
      </div>
      <div style="border-top:6px solid #F7F4F8;color:#888;">取消</div>
    </van-popup>
  </div>
</template>
<script>
import { GetBusinessAuditApprovalbyhotel, PostBusinessAuditApprovalcancel, PostBusinessAuditApprovalCommit } from '@/api/api_equip';
import { Dialog, Toast } from 'vant';
import { GetSystemDictDataByType } from '@/api/api_query';
export default {
    data() {
        return {
            applyList: [],
            auditApprovalType: {},
            showPay: false,
            payList: [{ type: 'online', title: '在线扫码付款' }, { type: 'pay', title: '对公转账' }],
            statusAll: { 0: '申请中', 1: '被驳回', 2: '申请通过', 3: '已取消' }
        };
    },
    created() {
        this.$store.commit('setHeader', '申请列表');
        this.getType();
    },
    methods: {
        getType() {
            GetSystemDictDataByType({ dictType: 'business_audit_approval_type' }).done((res) => {
                res.data.map((item) => {
                    this.auditApprovalType[item.dictValue] = item.dictLabel;
                });
                this.getApplyList();
            });
        },
        getApplyList() {
            GetBusinessAuditApprovalbyhotel({ hotelId: this.$route.query.hotelId }).done((res) => {
                res.data.map((item) => {
                    item.hotel.address = item.hotel.province + item.hotel.city + item.hotel.county;
                });
                this.applyList = res.data;
            });
        },
        // 重新提交
        rePush(item) {
            Dialog.confirm({
                title: '确定新提交?'
            })
                .then(() => {
                    PostBusinessAuditApprovalCommit({ auditApprovalId: item.id }).done((res) => {
                        Toast.success(res.msg);
                        this.getType();
                    });
                })
                .catch(() => {
                    console.log('不通过');
                });
        },
        // 工单流程取消
        cancelApply(id) {
            Dialog.confirm({
                title: '取消申请?',
                showCancelButton: 'true'
            })
                .then(() => {
                    PostBusinessAuditApprovalcancel({ auditApprovalId: id }).done((res) => {
                        Toast.success(res.msg);
                        this.getType();
                    });
                })
                .catch(() => {});
        },
        // 立即付款
        rightPay(item) {
            this.payInfo = item;
            this.showPay = true;
        },
        // 选择支付方式
        choosePay(type) {
            this.$router.push({
                path: '/pay-page',
                query: {
                    auditApprovalId: this.payInfo.id,
                    type: type
                }
            });
            this.showPay = false;
        },
        // 新建申请
        goNext(url, item) {
            this.$router.push({
                path: url,
                query: {
                    hotelId: this.$route.query.hotelId,
                    auditApprovalId: item.id,
                    applyType: item.applyType
                }
            });
        }
    }
};
</script>
<style scoped>
.scroll {
    margin: 10px 0;
}
.nothing {
    margin: 100px auto;
    line-height: 50px;
    font-size: 0.8rem;
}
.info-hotel {
    font-size: 0.8rem;
    display: flex;
    justify-content: space-between;
    padding: 10px;
    border-bottom: 1px solid #efeff4;
    color: #b2b2b2;
}
.hotel-left {
    display: flex;
    align-items: center;
    text-align: left;
    width: 80%;
}
.left-img {
    width: 80px;
    height: 80px;
    border-radius: 4px;
}
.left-title {
    height: 86px;
}
.hotel-right {
    display: flex;
    flex-direction: column-reverse;
    justify-content: inherit;
    text-align: right;
    width: 26%;
}
/* 驳回原因 */
.reason {
    color: #000;
    font-size: 0.7rem;
    text-align: left;
    padding: 10px;
    line-height: 20px;
}
.reason-right {
    display: flex;
    justify-content: space-between;
    text-align: center;
}
.edit-reason {
    padding: 0 2px;
    width: 16%;
    border: 1px solid #ff7d41;
    border-radius: 2px;
    color: #ff7d41;
    font-size: 0.6rem;
}
.c_ff7d41 {
    font-size: 0.8rem;
    color: #ff7d41;
    margin: 26px 0;
}
.c_fff {
    font-size: 0.8rem;
    color: #fff;
    background: #ff7d41;
    line-height: 40px;
    border-radius: 4px;
    margin: 80px 10px;
}
.c_000 {
    color: #000;
    font-size: 0.9rem;
    max-width: 90px;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.c_ce {
    color: #cecece;
}
.c_f60 {
    color: #ff7d41;
}
.adr {
    font-size: 0.6rem;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    height: 46%;
    margin: 2px 0;
}
.edit-reason {
    padding: 4px 8px;
    border: 1px solid #ff7d41;
    border-radius: 2px;
    color: #ff7d41;
    font-size: 0.6rem;
    margin: 0 6px;
}
</style>



